.container{min-height:100vh;display:grid;grid-template-rows:1fr auto 1fr}.title{margin:24px 0 !important}.subtitle{margin:12px 0}.containerInner{display:flex;gap:48px;align-items:flex-start;align-self:center}.column{flex:1}.column img{width:100%;height:100%;position:absolute;inset:0;object-fit:cover;color:rgba(0,0,0,0);z-index:1}@media(max-width: 768px){.containerInner{flex-wrap:wrap}.column{width:100%;flex:auto}}.label,.result{display:block;position:relative;border-radius:8px;overflow:hidden;margin-bottom:36px}.label::before,.result::before{display:block;content:"";padding-bottom:100%}.label{cursor:pointer}.label::after{content:"Завантажити зображення";align-items:center;justify-content:center;color:#fff;position:absolute;z-index:3;inset:0;backdrop-filter:blur(4px);background-color:rgba(102,102,102,.5);pointer-events:none;opacity:0;display:flex;transition:all 400ms}.label:hover::after{opacity:1}.bg{position:absolute;inset:0;background-color:#666;pointer-events:none;z-index:2}.bg.hidden{display:none}.resultButton{position:absolute;inset:0;transition:all 400ms;opacity:0;backdrop-filter:blur(4px);background-color:rgba(102,102,102,.5);border:none;box-shadow:none;z-index:10;font-size:16px;color:#fff;cursor:pointer}.resultButton:hover{opacity:1}